.green {
  background: #0D5822;
  height: 100vh;
  width: 100vw;

}

.install {
  height: 100%;

  display: flex;
  justify-content: center;
  align-items: center;
}

* {
  -ms-user-select: none;
  -moz-user-select: none;
  -khtml-user-select: none;
  -webkit-user-select: none;
}

.modal {
  display: flex;
  align-items: center;
  justify-content: center;
}

.fade {
  background: rgba(255, 255, 255, 0.8);
  border: none;
  outline: none;
  width: 280px;
  height: 230px;
  border-radius: 8px;
  display: grid;
  grid-template-rows: 3.8fr 1fr;
  grid-template-columns: 1fr;


  .mainFade {
    padding: 20px 20px 0;

    h2 {
      font-family: Arial;
      font-size: 20px;
      line-height: 22px;
      text-align: center;
      color: #000000;
      font-weight: 700;
      margin: 0;
      padding: 0;
    }

    .inputs {
      margin-top: 20px;
      display: flex;
      justify-content: center;

    }

    p {
      margin-top: 10px;
      font-family: Arial;
      font-size: 12px;
      text-align: center;
      color: #979797;
    }
  }
}

.buttons {
  border-top: 1px solid rgba(60, 60, 67, 0.38);
  display: grid;
  grid-template-columns: 1fr 1fr;

  button {
    background: none;
    border: none;
    font-weight: 600;

    &:last-child {
      font-weight: 800;
      border-left: 1px solid rgba(60, 60, 67, 0.38);
    }
  }
}


.ldsRoller {
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);

  display: inline-block;
  width: 80px;
  height: 80px;
}

.ldsRoller div {
  animation: ldsRoller 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  transform-origin: 40px 40px;
}

.ldsRoller div:after {
  content: " ";
  display: block;
  position: absolute;
  width: 7px;
  height: 7px;
  border-radius: 50%;
  background: #fff;
  margin: -4px 0 0 -4px;
}

.ldsRoller div:nth-child(1) {
  animation-delay: -0.036s;
}

.ldsRoller div:nth-child(1):after {
  top: 63px;
  left: 63px;
}

.ldsRoller div:nth-child(2) {
  animation-delay: -0.072s;
}

.ldsRoller div:nth-child(2):after {
  top: 68px;
  left: 56px;
}

.ldsRoller div:nth-child(3) {
  animation-delay: -0.108s;
}

.ldsRoller div:nth-child(3):after {
  top: 71px;
  left: 48px;
}

.ldsRoller div:nth-child(4) {
  animation-delay: -0.144s;
}

.ldsRoller div:nth-child(4):after {
  top: 72px;
  left: 40px;
}

.ldsRoller div:nth-child(5) {
  animation-delay: -0.18s;
}

.ldsRoller div:nth-child(5):after {
  top: 71px;
  left: 32px;
}

.ldsRoller div:nth-child(6) {
  animation-delay: -0.216s;
}

.ldsRoller div:nth-child(6):after {
  top: 68px;
  left: 24px;
}

.ldsRoller div:nth-child(7) {
  animation-delay: -0.252s;
}

.ldsRoller div:nth-child(7):after {
  top: 63px;
  left: 17px;
}

.ldsRoller div:nth-child(8) {
  animation-delay: -0.288s;
}

.ldsRoller div:nth-child(8):after {
  top: 56px;
  left: 12px;
}

.downloadBtn{
  background: #fff;
  font-size: 14px;
  font-weight: bold;
  padding: 10px 20px;
  outline: none;

  &:hover{
    background: #dedede;
  }
}

@keyframes ldsRoller {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
